@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:#eeeeee;background:var(--Ligght-main)}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.Container{display:flex;flex-direction:column;align-items:center;max-width:1200px;width:100%;height:100%;margin:0 auto;padding:0 20px;box-sizing:border-box;overflow:hidden}:root{--Dark-main-decimal: 177, 178, 180;--Dark-main: #444446;--Default-main: #cccccc;--Ligght-main: #eeeeee;--parallax-perspective: 8px;--z-distance: 3px;--header: 118px}

.Header{position:-webkit-sticky;position:sticky;width:100%;top:0;background-color:rgba(var(--Dark-main-decimal), 0.9);overflow:hidden;z-index:2}.Header .Header-list{display:flex;list-style:none;align-items:center;justify-content:space-between;margin:unset;padding:unset}.Header .Header-list .Header-list-line .My-img{height:80px;width:80px;border-radius:100px;object-fit:cover;margin:10px 10px 10px 0px}.Header .Header-list .Header-list-line .Header-list-name{color:var(--Dark-main)}.Header .Language--btn{position:absolute;top:0;right:0;color:var(--Default-main);padding:5px 5px 30px 30px;background-color:transparent;border:none}.Header .Language--btn:focus{outline:0}.Header .Language--btn::before{content:"";position:absolute;top:-35px;right:-35px;height:50px;width:100px;transform:rotate(45deg);background-color:var(--Dark-main);z-index:-1}@media only screen and (min-width: 800px){.Header-list{width:100%;max-width:300px}}

.About{height:100%;max-height:320px}.About .About-text{text-align:center;margin-bottom:30px}

.Skills{display:flex;justify-content:center;align-items:center;width:100%;height:100%;max-height:320px}.Skills .Container{padding:unset}.Background{height:calc((100vh - var(--header)) / 2);width:100%;background:url(/Life-Description/static/media/SkillsBackground.874a400e.jpg);background-repeat:no-repeat;background-size:cover;overflow:hidden}.Foreground{margin-top:100px;display:flex;flex-direction:row;flex-wrap:wrap;width:100%}.Foreground--content{display:flex;justify-content:space-around;width:100%}.Skills--logo{width:40px;height:min-height;object-fit:contain}@media only screen and (min-width: 800px){.Skills{max-width:50%}}

.Education--list{list-style:none;margin:unset;padding:unset}.Education--list--line{margin:unset;padding:unset}.Education--list--line p{margin:5px}.Education--list--Date{color:var(--Default-main)}.Education--list--box{display:flex;align-items:flex-start;margin:20px 0px}.Education--logo{width:80px;object-fit:contain;margin-right:20px}

.Project{display:flex;align-items:center}.Project--link{text-decoration:none}.Desktop--picture{position:relative;width:100%;display:block;object-fit:cover}.Desktop--picture .Overlay{position:absolute;text-align:center;top:0;bottom:0;left:0;right:0;height:100%;width:100%;font-family:'Playfair Display', serif;font-weight:600;font-size:24px;opacity:0;transition:.5s ease;background-color:rgba(0,0,0,0.4);margin:unset;padding:30px;box-sizing:border-box;color:var(--Default-main)}.Desktop--picture .Overlay:hover{opacity:1}.Project--img{width:100%;object-fit:cover}.Project--name{text-align:center}@media only screen and (max-width: 800px){.Desktop{display:none}}@media only screen and (min-width: 800px){.Projects{max-width:50%}}

.Contacts--list{display:flex;list-style:none;width:100%;padding:unset;margin:unset}.Contacts--list--box{margin:0 15px}.Contacts--hyperlink{display:flex;width:100%;align-items:center;text-decoration:none;color:black}.Contacts--hyperlink :hover{cursor:pointer}.Contacts--logo{width:30px;height:30px;margin-right:15px}@media only screen and (max-width: 800px){.Contacts--list{flex-direction:column;flex-wrap:wrap;justify-content:space-between;margin-bottom:20px}}

@media only screen and (min-width: 800px){.Media{display:flex;flex-direction:row;align-items:center;justify-content:space-between;max-width:1200px;width:100%;height:100%;margin:0 auto;margin-top:30px;padding:0 20px;box-sizing:border-box;overflow:hidden}}

